<template>

    <div class="mypanel">
        <div class="hd">
           订单管理
        </div>
        <div class="bd">
        <div class="search-options">

            <table  cellspacing="1" width="100%">
                <tbody>
                <tr>
                    <td>订单号 <input name="ddno" @keyup.enter="search" v-model="ddno" class="input-txt" type="text"  />

                        <el-button @click="search" type="primary" icon="el-icon-search">搜索</el-button>
                    </td>
                </tr>
                </tbody>
            </table>

        </div>
        <!-- 搜索控件结束 -->
        <div class="clear"></div>

        <div class="cst-list">

            <div v-for="order in listOrder" :key="order.id" class="item">

                <div class="title">
                  {{order.ddno}}
                </div>
                <div class="subtitle">
                    <div class="it">{{order.createtime}}</div>
                    <div class="it">下单人:{{order.puraccount}}({{order.purname}})</div>

                    <div class="it">状态:
                        <span v-if="order.state==1">待付款</span>
                        <span v-if="order.state==2">待派送</span>
                        <span v-if="order.state==3">待评价</span>
                        <span v-if="order.state==4">已评价</span>
                        <span v-if="order.state==5">已取消</span>
                        <span v-if="order.state==8">已分配送餐员</span>

                    </div>
                    <div class="it">
                        <el-button type="danger" v-if="order.state==2" size="mini" @click="showDlg(order,'dispatchDlg')">分配送餐员</el-button>
                        <el-button type="primary" size="mini" @click="showDlg(order,'infoDlg')">详细</el-button>
                    </div>
                </div>

                <div v-for="od in order.orderDetail" :key="od.id" class="bd clearfix">
                    <div class="img-area fnleft">
                        <img :src="hostHead+od.images[0]"/>
                    </div>
                    <div class="des-area fnleft">
                        <div>{{od.spname}}</div>
                        <div>{{od.price}}X{{od.count}}</div>
                        <div>商户:{{od.shaccount}}{{od.shname}}</div>
                        状态:<span v-if="od.state==1">待付款</span>
                        <span v-if="od.state==2">待派送</span>
                        <span v-if="od.state==3">待评价</span>
                        <span v-if="od.state==4">已评价</span>
                        <span v-if="od.state==5">已取消</span>
                        <span v-if="od.state==8">已分配送餐员</span>

                    </div>

                    <div class="des-area fnright">

                        <pop-dialog-button v-if="od.state==8" @refresh="search" title="发货" :inline="false" :url="'/admin/fahuo?id='+order.id" :xdata="{oiid:od.id}" css-class="action-btn">送餐</pop-dialog-button>
                        <pop-dialog-button @refresh="search"  title="订单详情"  :inline="false"  :url="'/huiyuan/shorderdetails?id='+order.id" css-class="action-btn">订单详情</pop-dialog-button>

                    </div>
                </div>

            </div>

        </div>

        <div v-if="listOrder!=null&&listOrder.length==0" class="no-data">
            <div class="bd">
                <img class="img" src="../admin/images/nodata.png"/>
                <div class="text">暂无订单数据</div>
            </div>

        </div>


        <div class="clear"></div>
        <el-pagination
            background
            @size-change="pagesizeChange"
            @current-change="pageindexChange"
            :current-page="pageindex"
            :page-sizes="[pagesize]"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
        </div>


        <shorderdetails ref="infoDlg"></shorderdetails>
        <Dispatchshorder ref="dispatchDlg" @over="search"></Dispatchshorder>
    </div>
</template>

<script>
    import PopDialogButton from "../common/mydialog/PopDialogButton";
    import {ModalDialogUtils} from "@/util/vueutil";
    import Shorderdetails from "@/huiyuan/shorderdetails";
    import Dispatchshorder from "@/admin/dispatchshorder";

    export default {
        name: "shordermanager",
        mixins:[ModalDialogUtils],
        data(){
            return {
                ddno:"",
                hostHead:this.$app.contextPath,

                selectedAll:false,
                listOrder:[],
                listUser:[],
                pageindex:1, //初始页
                pagesize:10,
                total:10
            }

        },
        props:{
            accountInfo:{
                type:Object
            }
        },
        methods:{
            pagesizeChange: function (pagesize) {
                this.pagesize = pagesize;
            },
            pageindexChange: function(pageindex){
                this.pageindex = pageindex;
                console.log(this.pageindex);
                this.search();
            },
            async search(){
                let url="admin/shorder/list";
                let param={
                    currentpageindex:this.pageindex,
                    pagesize:this.pagesize,
                    ddno:this.ddno,
                    zyid:this.accountInfo.id
                };

                let {data:res}=await this.$http.post(url,param);
                if(res!=null){
                    let pageInfo=res.data;
                    this.total=pageInfo.total;
                    if(pageInfo.list!=null){
                        pageInfo.list.forEach(c=>{
                            c.rowSelected=false;
                        });
                    }
                    this.listOrder=pageInfo.list;
                    console.log("数据",this.listOrder);
                }
            },
            async deleteRec(){
                let url="admin/shorder/delete";
                let hasChecked=this.listOrder.some(c=>{
                    return c.rowSelected==true;
                });
                if(!hasChecked){
                    this.$alert('请选择需要删除的记录', '系统提示', {
                        confirmButtonText: '确定'
                    });
                    return;
                }

                let ids=this.listOrder.filter(c=>c.rowSelected).map(c=>c.id);
                console.log("ids",ids);
                let res= this.$confirm('是否要删除订单信息?', '系统提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                }).then(()=>{
                    this.$http.post(url, {ids}).then(res=>{
                        if (res.data != null && res.data.stateCode > 0) {
                            this.search();
                        }
                    });
                }).catch(()=>{});
            },
            selectedAllHandler(){
                console.log(this.selectedAll);
                this.selectedAll=!this.selectedAll;
                if (this.listOrder!=null) {
                    this.listOrder.forEach(c => {
                        c.rowSelected = this.selectedAll;
                    });

                }

            }
        },
        components:{
            Shorderdetails,
            Dispatchshorder,
            PopDialogButton
        },
        created(){
            this.search();
        }
    }
</script>

<style scoped>

</style>
