<template>
    <el-dialog
        v-if="visible"
        title="分配送餐员"
        :visible.sync="visible"
        width="70%"
        top="5vh"
        append-to-body
    >
        <el-card  class="box-card">

            <div style="margin:22px 30px; ">
                <el-steps :space="200" :active="3" finish-status="success">
                    <el-step title="下单"></el-step>
                    <el-step title="付款"></el-step>
                    <el-step title="派送"></el-step>
                    <el-step title="收货评价"></el-step>
                </el-steps>
            </div>

            <table border="1" class="smart-table">
                <tr>
                    <td class="tlabel">订单编号</td>
                    <td>{{order.ddno}}</td>
                    <td class="tlabel">下单时间:</td>
                    <td>
                        {{order.createtime}}

                    </td>
                </tr>

                <tr>
                    <td class="tlabel">总金额</td>
                    <td >{{order.totalfee}}¥</td>

                    <td class="tlabel">下单人</td>
                    <td >{{order.puraccount}}({{order.purname}})</td>

                </tr>
                <tr>
                    <td class="tlabel">派送员</td>
                    <td colspan="3">
                        <div style="width:300px; " v-if="order.state==2">
                            <el-select v-model="zyid">
                                <el-option v-for="item in listUsers" :value="item.id" :label="item.realname+'---'+item.username"></el-option>
                            </el-select>
                        </div>


                    </td>
                </tr>
            </table>
            <table   width="100%" border="0" cellspacing="0" cellpadding="0" class="ui-record-table">


                <thead>

                <tr>
                    <th>名称</th>
                    <th>价格</th>
                    <th>数量</th>
                    <th>状态</th>
                </tr>
                </thead>
                <tbody>

                <tr v-for="od in order.orderDetail">
                    <td><img width="80" height="80" :src="hostHead+od.images[0]"/>
                        {{od.spname}}</td>
                    <td>{{od.price}}元</td>
                    <td>{{od.count}}</td>
                    <td>
                        <span v-if="od.state==1">待付款</span>
                        <span v-if="od.state==2">已付款</span>
                        <span v-if="od.state==3">
                            物流号:{{od.wlno}}
                        </span>
                        <span v-if="od.state==4">已完成</span>
                        <span v-if="od.state==5">已取消</span>



                    </td>
                </tr>

                <tr >

                    <td colspan="4">
                        <el-button  @click="dispatchHandler" type="primary"><i class="el-icon-arrow-right"></i>分配</el-button>
                    </td>
                </tr>



                </tbody>
            </table>



        </el-card>
    </el-dialog>
</template>

<script>
export default {
    name: "shorderdetails",
    data(){
        return {
            visible:false,
            oiid: this.$route.query.oiid,
            wlno: '',
            zyid:1,
            listUsers:[],
            hostHead:this.$app.contextPath,
            order: {},
            receaddress: null
        }
    },


    methods:{

        async show(item){
            if (item == null)
                return this.$message.error("订单信息不能为空");
            this.order=item;
            await this.getUsers();
            await this.getOrderInfo();
            this.visible = true;
        },
        async getUsers(){

            let {data:res}=await this.$http.post("admin/users/list",{"ispaged":"-1",roleid:2});

            if (res.stateCode<0){
                this.$message.error(res.des);
                return ;
            }
            this.listUsers=res.data;
            if(this.listUsers!=null&&this.listUsers.length>0)
                this.zyid=this.listUsers[0].id;

        },
        async getOrderInfo(){

            let {data:res}= await this.$http.post("admin/shorder/load",{id:this.order.id});
            if (res.stateCode<0){
                this.$message.error(res.des);
                return ;
            }
            console.log("订单数据",res);
            this.order=res.data;

        },
        async dispatchHandler() {


            this.$confirm('是否要派送?', '系统提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
            }).then(async () => {
                let {data: res} = await this.$http.post("admin/shorder/dispatch", {
                    id: this.order.id,
                    zyid: this.zyid
                });

                if (res.stateCode < 0) {
                    this.$message.error(res.des);
                    return;
                }
                this.$message.success("派送完成");
                this.$emit("over");
                this.visible=false;
            }).catch(() => {
            });


        },
        async getAddress(){

            let {data:res}=await this.$http.post("admin/receaddress/load",{id:this.order.addid});

            if (res.stateCode<0){
                this.$message.error(res.des);
                return ;
            }
            this.receaddress=res.data;
            console.log("receaddress",this.receaddress);

        }

    },
    async created(){

    }


}
</script>

<style scoped>

</style>
